body{margin:0px;}
.form{
	position: absolute;
	width: 100%;
	height: 100%;
}


.slideshow {
  position: fixed;
  z-index: 1;
  height: 100%;
  width: 100%;
  background-color:#333;
  
}
  
  
  .slider-track {
    transition: all 1s cubic-bezier(0.7, 0, 0.3, 1);
  }
  
  .item {
    height:100%;
    position: relative;
    z-index: 1;
  }
  img {
      width: 100%;
	  height:46.8vw;
      transition: all 1s cubic-bezier(0.7, 0, 0.3, 1);
      transform: scale(1);
   }
    
  &.slick-active img {
      transform: scale(1);
  }
  
  .slider_mask{
	  position:absolute;
	  z-index: 2;
	  height: 100%;
      width: 100%;
	  background-image: linear-gradient(to bottom,rgba(0,0,0,0.8),rgba(255,255,255,0));
	  opacity: 1; 
	  top:0;
	  left:0;
  }
  .slider_auto_head{
	  position:absolute;
	  z-index:3;
	  color:white;
	  text-decoration: none;
  }
  
  .menu {
   text-align: right;
   
	    
}
.hr_left{
	position:absolute;
	width:39vw;
	height:0.1px;
	margin-top:2.2vw;
}
.hr_right{
	position:absolute;
	width:42.85vw;
	margin-top:2.2vw;
	margin-left:57vw;
	
}
.menu__links{
	position:absolute;
	top:2.6vw;
	width:100vw;
	left:0vw;
	text-align: center;
	
}
.slider_auto_logo{
	position:relative;
	top:-1.5vw;
	height:5vw;
	text-align:center;
	padding-left:1vw;
	padding-right:2vw;
	font-size:4vw;
}
.slider_auto_logo_text{
	height:4vw;
	width:16vw;
	margin-left:2vw;
}
.menu__links-item {
    display: inline-block;
    color: #fdfdfd;
    font-family:'Lexend Exa', sans-serif;
    font-size: 1vw;
    line-height: 0.5vw;
    padding: 1vw 0.4vw;
    text-transform: uppercase;
    text-decoration: none;
	vertical-align:top;
}

.menu__links-item:hover {
    text-decoration: none;
	color:#bfbebe;
}
.menu__icon {
    display: none;
    width: 4px;
    height: 2vw;
	top:9vw;
	left:85vw;
    position: relative;
    cursor: pointer;
}

.menu__icon span {
    display: block;
    position: absolute;
    height: 0.1vw;
    width: 4vw;
    background: #ffffff;
    border-radius: 9px;
    opacity: 1;
    left: 0;
	padding:2px;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
}

.menu__icon span:nth-child(1) {
    top: 0px;
}

.menu__icon span:nth-child(2), .menu__icon span:nth-child(3) {
    top: 13px;
}

.menu__icon span:nth-child(4) {
    top: 26px;
}
.menu.menu_state_open .menu__icon span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.menu.menu_state_open .menu__icon span:nth-child(2) {
  transform: rotate(45deg);
}

.menu.menu_state_open  .menu__icon span:nth-child(3) {
  transform: rotate(-45deg);
}

.menu.menu_state_open  .menu__icon span:nth-child(4) {
  top: 180px;
  width: 0%;
  left: 50%;
}

.menu.menu_state_open .menu__links {
  display: block;  
}

@media screen and (max-width: 999px) {
  .menu__icon{
    display: inline-block;
  }
  .slider_auto_logo{
	display: none;
	padding:0;
}
  .menu__links {
    position: fixed;
    display: none;
    top: 0;
    right: 0;
    left: 0;
    margin-top: 13vw;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1000;
    overflow: auto;
  }
  
  .menu__links-item {
    display: block;
    padding: 10px 0;
    text-align: center;
    color: #ffffff;
  }
}
  
